import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { List, ResourceListItem } from '@Green-Dot-Corporation/eureka-ui-lists';
import { Callout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import WindowContainer from '../window-container/WindowContainer';
import Help from '../svg/Help';
import Support from '../svg/Support';
import Message from '../svg/Message';
import WriteAMessage from '../svg/WriteAMessage';
import LeaveMessage from '../leave-message/LeaveMessage';
import GenericMessage from '../generic-message/GenericMessage';
import URL from '../../const/urlConst';

// Styles
import './styles/choose-option-window.scss';

const { LOGIN } = URL;
class ChooseOptionWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        ewt: PropTypes.number,
        accountIdentifier: PropTypes.string,
        i18n: PropTypes.object,
    };

    constructor(props) {
        super(props);
        const { accountIdentifier } = props;
        this.isLoggedIn = !!accountIdentifier;
    }

    state = {
        shouldShowChooseOptionWindow: true,
        shouldShowConnectingPage: false,
        shouldShowLeaveMessagePage: false,
    };

    render() {
        const {
            shouldShowChooseOptionWindow,
            shouldShowConnectingPage,
            shouldShowLeaveMessagePage,
        } = this.state;

        return (
            <>
                {shouldShowConnectingPage && this.renderConnectingPage()}
                {shouldShowLeaveMessagePage && this.renderLeaveMessagePage()}
                {shouldShowChooseOptionWindow &&
                    this.renderChooseOptionWindow()}
            </>
        );
    }

    renderChooseOptionWindow = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
                {...restProps}
            >
                {this.renderCallout()}
                {this.renderOptions()}
            </WindowContainer>
        );
    };

    renderCallout = () => {
        const { contextCls, i18n } = this.props;
        // TODO: replace the title and meta when localization available
        const headText = i18n.getTranslation('chooseOptionWindow.title');

        return (
            <Callout
                contextCls={cx(`${this.baseCls}__header-container`, contextCls)}
                renderIcon={this.renderSvg}
                title={headText}
            />
        );
    };

    renderConnectingPage = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <GenericMessage
                contextCls={cx(
                    this.baseCls,
                    contextCls,
                    'web-chat-generic-message--withNoSubText',
                )}
                shouldShowMinimizeBtn
                renderIcon={() => <WriteAMessage />}
                headText="Connecting you with a support agent.
                Please wait."
                {...restProps}
            />
        );
    };

    renderLeaveMessagePage = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <LeaveMessage
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowEmailInput={!this.isLoggedIn}
                {...restProps}
            />
        );
    };

    renderSvg = () => {
        const { contextCls } = this.props;

        return (
            <Help
                contextCls={cx(
                    `${this.baseCls}__header-container-svg`,
                    contextCls,
                )}
            />
        );
    };

    renderOptions = () => {
        const { contextCls, ewt } = this.props;
        // TODO: replace the title and meta when localization available
        const supportMessageLoggedIn =
            ewt > 10
                ? 'Currently experiencing longer than usual wait times'
                : `Estimated wait time ${ewt} minutes.`;
        const data = [
            {
                id: 'option-support',
                meta: this.isLoggedIn
                    ? supportMessageLoggedIn
                    : 'To better serve you, please log in to chat.',
                text: this.isLoggedIn
                    ? 'Chat with a support agent'
                    : 'Log in to chat with an agent',
                handleClick: this.handleSupportOptionClick,
                renderIcon: this.renderSvgSupport,
            },
            {
                id: 'option-message',
                text: 'Leave a message',
                meta: 'Describe your issue and we will get back to you.',
                handleClick: this.handleMessageOptionClick,
                renderIcon: this.renderSvgMessage,
            },
        ];

        return (
            <List
                contextCls={cx(
                    `${this.baseCls}__content-container`,
                    contextCls,
                )}
                data={data}
                renderListItem={this.renderOptionsListItem}
            />
        );
    };

    renderOptionsListItem = (item) => {
        return (
            <ResourceListItem
                contextCls={`${this.baseCls}__${item.id}`}
                text={item.text}
                meta={item.meta}
                onClick={item.handleClick}
                renderIcon={item.renderIcon}
                isInteractive
                shouldIconUseColor
                shouldRenderAfterIcon
            />
        );
    };

    renderSvgSupport = () => (
        <Support contextCls={`${this.baseCls}__svg-container`} />
    );

    renderSvgMessage = () => (
        <Message contextCls={`${this.baseCls}__svg-container`} />
    );

    handleSupportOptionClick = () => {
        if (this.isLoggedIn) {
            this.setState({
                shouldShowChooseOptionWindow: false,
                shouldShowConnectingPage: true,
                shouldShowLeaveMessagePage: false,
            });
        } else {
            window.location.href = LOGIN;
        }
    };

    handleMessageOptionClick = () => {
        this.setState({
            shouldShowChooseOptionWindow: false,
            shouldShowConnectingPage: false,
            shouldShowLeaveMessagePage: true,
        });
    };

    baseCls = 'choose-option-window';
}

export default ChooseOptionWindow;
